import Home from '../pages/Home/Home.vue'
import Routine from '../pages/Routine/Routine.vue'
import Structure from '../pages/Structure/Structure.vue'
import Learn from '../pages/Learn/Learn.vue'
import Solve from '../pages/Solve/Solve.vue'
import Plug from '../pages/Plug/Plug.vue'
import Test from '../pages/Test/Test.vue'
import Case from '../pages/Case/Case.vue'
export default [{
    path: '/home',
    component: Home,
  },
  {
    path: '/case',
    component: Case,
  },
  {
    path: '/routine',
    component: Routine,
  },
  {
    path: '/structure',
    component: Structure,
  },
  {
    path: '/learn',
    component: Learn,
  },
  {
    path: '/solve',
    component: Solve,
  },
  {
    path: '/plug',
    component: Plug,
  },
    {
      path: '/test',
      component: Test,
    },
  {
    path: '/:id',
    redirect: '/home'
  },
   {
     path: '/',
     redirect: '/home'
   }
]